.App {
  display: flex;
  height: 100vh;
  flex-direction: column;
}

body {
  margin: 0;
  padding: 0;
}

.inputs {
  height: 400px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #a8dadc;
}

.inputs input {
  width: 200px;
  height: 50px;
  font-size: 18px;
  margin: 10px;
  padding-left: 10px;
}

.inputs button {
  border: none;
  width: 150px;
  height: 50px;
  background-color: #457b9d;
  color: white;
  border-radius: 10px;
  font-size: 15px;
  border: 2px solid black;
}

button:hover {
  cursor: pointer;
}

.listOfFriends {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 20px;
}

.friendContainer {
  display: flex;
  flex-direction: row;
  margin: 10px;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.friend {
  width: 400px;
  height: 60px;
  background-color: #457b9d;
  color: white;
  display: flex;
}

.friend h3 {
  margin-left: 15px;
}

.friendContainer button {
  width: 80px;
  height: 60px;
  border: 2px solid black;
  background-color: #457b9d;
  color: white;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

#removeBtn {
  border-left: None;
}

.titleHeader {
  color: black;
}
